<template>
  <div>
    <ul class="mt-lg-5">
      <li v-for="listItem in list">
        <div class="row">
          <div class="col-sm-9">
            <!-- <span>{{listItem.id}}.&nbsp;</span> -->
            <input v-on:click="completedItem(listItem)" type="checkbox" v-bind:checked="listItem.checked" v-bind:id="listItem.id">
            <label v-bind:for="listItem.id" v-bind:class="{checked: listItem.checked}">{{listItem.message}}</label>
          </div>
          <div class="col-sm-3">
            <button class="btn btn-outline-dark" v-on:click="updateItem(listItem.id)" data-toggle="modal" data-target="#exampleModal">
              <i class="material-icons">create</i>
            </button>
            <button class="btn btn-outline-danger" v-on:click="deleteItem(listItem.id)"><i class="material-icons">delete</i></button>
          </div>
        </div>
      </li>
    </ul>
      <!-- Modal -->  
      <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="exampleModalLabel"><i class="material-icons">create</i> Edit</h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <!-- Edit item here -->
            <div class="modal-body">
              <input type="text" class="form-control form-control-lg w-50 ml-auto mr-auto text-center" v-model=editItemMsg>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-primary" data-dismiss="modal" v-on:click="saveEdit()">Save</button>
              <button type="button" class="btn btn-outline-danger" data-dismiss="modal">Cancel</button>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  props: {
    list: {
      type: Array,
      required: true
    }
  },
  methods: {
    deleteItem: function(id){
      console.log(id);
      this.$emit('deleteItem', id);
    },
    updateItem(id){
      console.log("Updating item with id: "+id);
      let item = this.list.filter(item => item.id === id)[0];
      console.log(item.message);
      this.editItemMsg = item.message;
      this.editItemId = id;
    },
    saveEdit(){
      console.log(this.editItemId, this.editItemMsg);
      this.$emit("saveEdit", {
        id: this.editItemId,
        updatedMsg: this.editItemMsg
      });
    },
    completedItem: function(item){
      this.$emit('updateUI', item.id);
    }
  },
  data(){
    return{
      editItemId: null,
      editItemMsg: null
    }
  }
}
</script>

<style scoped>
  ul{
    padding: 0px;
  }
  ul li{
    list-style-type: none;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    padding: 1rem;
  }
  li:nth-child(odd){
    background: #f2e7ff;
  }
  li:nth-child(even){
    background-color:#ebdbff;
  }
  label:hover{
    cursor: pointer;
  }
  label{
    margin: 0 1rem;
    font-size: 20px;
  }
  input[type="checkbox"] {
    width: 22px;
    height: 22px;
    position: relative;
    top: 6px;
  }
  /* button{
    border: 2px solid red;
    background: transparent;
    color: red;
    transition: all .3s ease;
  }
  button:hover{
    color: white;
    background: darkred;
    transition: all .3s ease;
  } */
  .checked{
    opacity: .5;
    text-decoration: line-through;
  }
</style>



